@import "../../utils/colors.scss";
.Button {
  border: none;
  font-size: medium;
  color: $desert-storm;
  cursor: pointer;
  border-radius: 15px;
  &.button__medium {
    padding: 15px;
    margin: 5px;
    width: 25%;
  }
  &.button__large {
    padding: 13px;
    margin-top: 15px;
    margin-bottom: 10px;
    width: 70%;
  }
  &.color__blue {
    background-color: $atol;
  }
  &.ripple {
    background-position: center;
    transition: background 1s;
    &:hover {
      background: $elm radial-gradient(circle, transparent 1%, $elm 1%) center/15000%;
    }
    &:active {
      background-color: $nepal;
      background-size: 100%;
      transition: background 0s;
    }
  }
}


